import { Component, OnInit } from '@angular/core';
import { ApiService } from '../shared/api.service';
import { ReportData } from '../shared/model/report.data';
import { Router } from '@angular/router';

@Component({
  selector: 'my-reports',
  template: require('./reports.access.rejected.component.html'),
  styles: [require('./reports.access.rejected.component.scss')]
})

export class ReportsDetailAccessRejectedComponent implements OnInit {

  private rejectedTagReportData: ReportData[] = [];
  private error: string;

  constructor(private api: ApiService, private router: Router) {}

  ngOnInit() {
    this.api.getInfoAllRejectedSubeTag().subscribe((data: ReportData[]) => {
      this.rejectedTagReportData = data;
    }, err => {
      this.error = err._body;
    });
    console.log('Error : ' + this.error);
    console.log('rejected sube reporte data : ' + this.rejectedTagReportData);
  }

  gotoReports() {
    this.router.navigate(['/reports']);
  }

}



