import { Component, OnInit } from '@angular/core';
import { ApiService } from '../shared/api.service';
import { ReportData } from '../shared/model/report.data';
import { Router } from '@angular/router';

@Component({
  selector: 'my-reports',
  template: require('./reports.access.enter.today.component.html'),
  styles: [require('./reports.access.enter.today.component.scss')]
})

export class ReportsDetailAccessEnterTodayComponent implements OnInit {
  
  private employeeEnterTodayReportData: ReportData[] = [];
  private error: string;
  
  constructor(private api: ApiService, private router: Router) {}

  ngOnInit() {
      this.api.getInfoAllEmployeeEnterToday().subscribe((data: ReportData[]) => {
        this.employeeEnterTodayReportData = data;
      }, err => {
        this.error = err._body;
      });
      console.log('Error : ' + this.error);
      console.log('employee enter day reporte data : ' + this.employeeEnterTodayReportData);
  }

  gotoReports() {
    this.router.navigate(['/reports']);
  }

}



